.header-container {
  background: #111;
  background: var(--header-bg-color-light);
  border: 1.5px solid #111;
  border-radius: 0 0 8px 8px;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
  padding: 18px 20px;
  display: flex;
}

.dark-theme .header-container {
  background: #111;
}

.link {
  color: #111;
}

.container-link {
  height: 28px;
  flex-direction: row;
  align-items: center;
  gap: 8px;
  display: flex;
}

.link-logo-text {
  color: var(--logo-color-text);
  letter-spacing: -.56px;
  margin-left: 8px;
  font-family: DM Sans;
  font-size: 14px;
  font-weight: 500;
  line-height: 18px;
}

.icon-menu-open {
  stroke: #000;
}

.nav-link {
  display: none;
}

.link-logo {
  align-items: center;
  display: flex;
}

a.current {
  color: #111;
  background: #eac645;
  border-radius: 36px;
  padding: 8px 16px;
  font-weight: 700;
}

.container-shopping {
  display: none;
}

.switcher {
  align-items: center;
  margin-left: auto;
  margin-right: 14px;
  display: flex;
}

.switch {
  width: 40px;
  height: 20px;
  display: inline-block;
  position: relative;
}

.switch input {
  display: none;
}

.slider {
  cursor: pointer;
  background: linear-gradient(#4f2ee8, #dcdcdc);
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.slider:before {
  content: "";
  height: 18px;
  width: 18px;
  background-color: #fff;
  transition: all .4s;
  position: absolute;
  top: 1px;
  left: 1px;
}

input:checked + .slider {
  background: linear-gradient(#4f2ee8, #686868);
}

input:focus + .slider {
  box-shadow: 0 0 1px #4f2ee8;
}

input:checked + .slider:before {
  transform: translateX(20px);
}

.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

.menu-open {
  cursor: pointer;
  background-color: #fff;
  border: 0;
}

.icon-user-span {
  width: 37px;
  height: 37px;
  background-color: #fff;
  border-radius: 50px;
  justify-content: center;
  align-items: center;
  display: flex;
}

.icon-user {
  height: 19px;
  width: 19px;
  fill: var(--accent-color);
}

.icon-logo {
  height: 24px;
  width: 24px;
}

.icon-shopping {
  width: 20px;
  height: 20px;
  stroke: var(--logo-color-text);
}

.icon-switcher {
  width: 40px;
  height: 24px;
}

.icon-user-support {
  height: 26px;
  width: 23px;
}

.icon-menu-open {
  width: 28px;
  height: 28px;
  transition: var(--animation);
}

.icon-menu-open:-webkit-any(:hover, :focus) {
  stroke: var(--is-active-home-category-color);
}

.icon-menu-open:is(:hover, :focus) {
  stroke: var(--is-active-home-category-color);
}

.button-container {
  display: none;
  position: relative;
}

.register-btn:focus {
  opacity: 1;
  z-index: 1;
  transform: translateY(100%);
}

.register-btn {
  width: 184px;
  height: 48px;
  border: 1.5px solid var(--logo-color-text);
  background: var(--header-bg-color-light);
  color: var(--primery-bg-color-light);
  opacity: 0;
  z-index: -1;
  border-radius: 18px;
  justify-content: space-between;
  align-items: center;
  padding: 14px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: absolute;
  top: 0;
  transform: translateY(0);
}

.register-btn-text {
  color: #111;
  letter-spacing: -.56px;
  font-family: DM Sans;
  font-size: 14px;
  font-weight: 500;
  line-height: 18px;
}

.register-btn-icon {
  width: 20px;
  height: 20px;
  stroke: var(--hover-book-color);
}

.icon-display-none {
  display: none;
}

@media screen and (min-width: 375px) {
  .header-container {
    max-width: 375px;
  }
}

@media screen and (min-width: 768px) {
  .header-container {
    max-width: 768px;
    padding: 14px 32px;
  }

  .link-home-ul {
    justify-content: center;
    align-items: center;
    gap: 40px;
    margin-left: 40px;
    display: flex;
  }

  .icon-shopping {
    margin-left: 4px;
  }

  .nav-link {
    color: var(--logo-color-text);
    letter-spacing: -.56px;
    text-transform: uppercase;
    transition: var(--animation);
    align-items: center;
    font-family: DM Sans;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: 18px;
    display: flex;
  }

  .nav-link:hover {
    color: var(--is-active-home-category-color);
  }

  .nav-link.current:hover {
    color: var(--header-bg-color-light);
  }

  .user-btn {
    width: 184px;
    height: 45px;
    background: #4f2ee8;
    border: 0;
    border-radius: 36px;
    justify-content: space-between;
    align-items: center;
    padding-left: 12px;
    padding-right: 12px;
    display: flex;
  }

  .user-btn-text {
    color: #fff;
    letter-spacing: -.44px;
    font-family: DM Sans;
    font-size: 22px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
  }

  .icon-user-support {
    fill: #fff;
  }

  .menu-open {
    display: none;
  }

  .button-container {
    display: block;
  }
}

@media screen and (min-width: 1440px) {
  .header-container {
    max-width: 1440px;
  }

  .link-home {
    display: flex;
  }

  .link-home-ul {
    margin-left: 92px;
  }
}

