  --accent-color: #4f2ee8;
  --hover-book-color: #eac645;
  --categories-text-color-light: rgba(17, 17, 17, .6);
  --categories-text-color-dark: rgba(255, 255, 255, .6);
  --logo-color-text: #111;
  --primery-bg-color-light: #f6f6f6;
  --primery-bg-color-dark: #202024;
  --primery-font: "DM Sans", Sans-serif;
  --primery-text-color-dark: #f3f3f3;
  --header-bg-color-light: #fff;
