  overflow: hidden;
}

.loader {
  width: 48px;
  height: 48px;
  border: 5px solid #fff;
  border-bottom-color: var(--is-active-home-category-color);
  box-sizing: border-box;
  border-radius: 50%;
  margin-left: auto;
  margin-right: auto;
